export default {
	data() {
		return {
			list: [],
			loading: false,
			finished: false
		}
	},
	onLoad() {

		this.init()

	},
	// onPageScroll(e) {
	//
	// 		console.log(e);
	//
	// },
	onReachBottom() {
		this.init()
	},
	methods: {
		/**
		 * 模拟数据更新
		 */
		init() {
			// 这里包含了滚动加载，文档地址 https://youzan.github.io/vant/#/zh-CN/list
			// 异步更新数据
			setTimeout(() => {
				for (let i = 0; i < 15; i++) {
					this.list.push(this.list.length + 1);
				}
				// 加载状态结束
				this.loading = false;

				// 数据全部加载完成
				if (this.list.length >= 40) {
					this.finished = true;
				}
			}, 500);
		},
		/**
		 * 处理预定按钮点击
		 * @param {any} 传入的对应实验室的信息  
		 */
		goBookLab(lab) {
			console.log('传入的实验室的信息是', lab)
			uni.navigateTo({
				url: `./lab-book?lab=${lab}`,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		/**
		 * 去实验室详情页面
		 * @param {any} 传入的对应实验室的信息  
		 */
		goLabDetails(lab) {
			console.log('传入的实验室的信息是', lab)
			uni.navigateTo({
				url: `./lab-details?lab=${lab}`,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
		
	}
}
